(function () {
    'use strict';

    angular
        .module('mangroosHub')
        .service('NotificationService', NotificationService);

    NotificationService.inject = ['$q', 'NotificationResource'];
    function NotificationService($q, NotificationResource) {
        this.getEmailNotifications = getEmailNotifications;
        this.getNotifications = getNotifications;
        this.getTaskNotifications = getTaskNotifications;

        ////////////////

        function getEmailNotifications() {
            var deferred = $q.defer();
            NotificationResource.emailNotification().get().$promise.then(function (res) {
                deferred.resolve(res);
            }, function (error) {
                deferred.reject(error);
            });
            return deferred.promise;
        }

        function getNotifications() {
            var deferred = $q.defer();
            NotificationResource.notification().get().$promise.then(function (res) {
                deferred.resolve(res);
            }, function (error) {
                deferred.reject(error);
            });
            return deferred.promise;
        }

        function getTaskNotifications() {
            var deferred = $q.defer();
            NotificationResource.taskNotification().get().$promise.then(function (res) {
                deferred.resolve(res);
            }, function (error) {
                deferred.reject(error);
            });
            return deferred.promise;
        }
    }
})();

(function () {
    'use strict';

    angular
        .module('mangroosHub')
        .factory('NotificationResource', NotificationResource);

    NotificationResource.inject = ['$resource'];
    function NotificationResource($resource) {
        var service = {
            emailNotification: emailNotification,
            notification: notification,
            taskNotification:taskNotification,
        };

        return service;

        ////////////////
        function emailNotification() {
            return $resource('app/notifications/emails/email-notification.json', {}, {
                get: {
                    method: 'GET',
                    transformResponse: function (res) {
                        return angular.fromJson(res);
                    }
                }
            });
        }

        function notification() {
            return $resource('app/notifications/notification.json', {}, {
                get: {
                    method: 'GET',
                    transformResponse: function (res) {
                        return angular.fromJson(res);
                    }
                }
            });
        }

        function taskNotification() {
            return $resource('app/notifications/tasks/task-notification.json', {}, {
                get: {
                    method: 'GET',
                    transformResponse: function (res) {
                        return angular.fromJson(res);
                    }
                }
            });
        }
    }
})();

